function request(params, isGetTonken) {
  // 
  var globalData = getApp().globalData; // 

  // if (!isGetTonken && globalData.isLanding) {
  //   globalData.requestQueue.push(params);
  //   return;
  // }

	if (Object.prototype.toString.call(params.data) == '[object Array]') {
		params.data = JSON.stringify(params.data);
	} else if (Object.prototype.toString.call(params.data) == '[object Number]') {
		params.data = params.data + '';
	}
	var needToken = false
	// if (params.url.indexOf("/p/") == 0 || params.url.indexOf("/user/registerOrBindUser") == 0) {
	// 	needToken = true
	// }

  wx.request({
    // url: config.domain + params.url,
		url: (params.domain ? params.domain : config.domain) + params.url,
    //
    data: params.data,
    header: {
      // 'content-type': params.method == "GET" ? 'application/x-www-form-urlencoded' : 'application/json;charset=utf-8',
      // 'Authorization': params.login ? undefined : uni.getStorageSync('token')
			'Authorization': uni.getStorageSync('token') ,
    },
    method: params.method == undefined ? "POST" : params.method,
    dataType: 'json',
    responseType: params.responseType == undefined ? 'text' : params.responseType,
    success: function (res) {
      if (res.statusCode == 200) {
        //params.callBack params.callBack(res.data)
        if (params.callBack) {
          params.callBack(res.data);
        }
      } else if (res.statusCode == 500) {
        uni.showToast({
          title: "",
          icon: "none"
        });
      } else if (res.statusCode == 401) {
        // 
        // globalData.requestQueue.push(params); // 

        // if (!globalData.isLanding) {
        //   globalData.isLanding = true; //token,

        //   getToken();
        // }
				uni.removeStorageSync('loginResult');
				uni.removeStorageSync('token');




				uni.hideLoading();
				if (!params.dontTrunLogin) {
					if (uni.getStorageSync('hadLogin')) {
						uni.showModal({
							title: "",
							content: "",
							cancelText: "",
							confirmText: "",
							success: res => {
								if (res.confirm) {
									// 
									var url = ''

									if (uni.getStorageSync('appType') == AppType.MP || uni.getStorageSync('appType') == AppType.MINI) {
										url = '/pages/login/login'
									} else {
										url = '/pages/accountLogin/accountLogin'
									}





									uni.navigateTo({
										url: url
									})
								} else {
									uni.switchTab({
										url: '/pages/index/index'
									})
								}
							}
						})
					} else {
						uni.showModal({
							title: "",
							content: "",
							cancelText: "",
							confirmText: "",
							success: res => {
								if (res.confirm) {
									// 







									uni.navigateTo({
										url: '/pages/login/login'
									})

								} else {
									uni.switchTab({
										url: '/pages/index/index'
									})
								}
							}
						})
				// 		// 
				//





				//
				// 		uni.navigateTo({
				// 			url: '/pages/login/login'
				// 		})
				//
					}
				}
      } else if (res.statusCode == 400 && !params.errCallBack) {
        uni.hideLoading();
				uni.showToast({
          title: res.data,
          icon: "none"
        });
      } else {
        //params.errCallBack params.errCallBack(res.data)
        if (params.errCallBack) {

          params.errCallBack(res);
        }
				uni.hideLoading();
      }

      // if (!globalData.isLanding) {
      //   uni.hideLoading();
      // }
    },
    fail: function (err) {
      uni.hideLoading();
			if (err.errMsg == 'request:fail abort') {
				console.log('~')
				return
			}
			setTimeout(() => {
				uni.showToast({
					// zheli
					title: "",
					icon: "none"
				});
			}, 1);
      // uni.showToast({
      //   title: "",
      //   icon: "none"
      // });
    }
  });
} //codetoken,


