/**
 * 
 * @param {Object} fn  
 * @param {String} code  code, 
 */
var mpLogin = function (fn, code) {
	//  res.code  openId, sessionKey, unionId
	request({
		login: true,
		url: '/appLogin',
		data: {
			principal: code,
			appType: AppType.MP, // 
		},
		callBack: result => {
			loginSuccess(result, fn)
		}
	}, true);

};

